// Polyfill must be applied before React is imported
import { useSyncExternalStore } from 'use-sync-external-store/shim'

// Apply polyfill to React before it's used anywhere
import * as ReactNamespace from 'react'
if (!ReactNamespace.useSyncExternalStore) {
  // @ts-ignore
  ReactNamespace.useSyncExternalStore = useSyncExternalStore
}

import React, { StrictMode } from 'react'
import { createRoot } from 'react-dom/client'
import './index.css'
import App from './App'
import { register } from './utils/registerServiceWorker'

// Register service worker for PWA functionality
register({
  onSuccess: (registration) => {
    console.log('Service worker registered successfully:', registration)
  },
  onUpdate: (registration) => {
    console.log('New content available, please refresh:', registration)
    // You could show a snackbar here to prompt user to refresh
  }
})

createRoot(document.getElementById('root')!).render(
  <StrictMode>
    <App />
  </StrictMode>,
)
